import './App.css';
import i18n from './i18n';
import { Trans, useTranslation } from 'react-i18next';
import GithubIcon from './GithubIcon';

function App() {
    const {t} = useTranslation();
    const changeLanguage = () => {
        let lng = 'en';
        if (i18n.language === 'en') {
            lng = 'ru';
        }
        i18n.changeLanguage(lng).then();
    }

    return (
        <div className="app">
            <header className="app-header">
                <div style={{marginRight: 16}}>Gd-statistics!</div>
                <div>
                    \(      )/
                    <button className="lang-button" aria-label="Switch language (en/ru)"
                            title="Switch language (en/ru)" onClick={() => changeLanguage()}>
                        
                    </button>
                </div>
            </header>
            <main className="main-content">
                <h2 className="main-headers">{t("aboutTitle")}</h2>
                <Trans i18nKey="aboutText">
                            Geometry Dash      , ,
                       ,     . .       
                    ..  !
                </Trans>
                <h2 className="main-headers">{t("locationTitle")}</h2>
                <Trans i18nKey="locationText">
                         <a href='https://github.com/DoubleCookies/gd-statistics/tree/master/Statistics'></a>.
                </Trans>
                <h2 className="main-headers">{t("workTitle")}</h2>
                <Trans i18nKey="workList">
                        :
                    <ul className="tight-list">
                        <li>   Geometry Dash    (10 featured-).  
                                   ,    .
                        </li>
                        <li>   featured-    epic- (  
                              featured    ).
                        </li>
                        <li>         ,  -
                             ,  -    ,  . .
                        </li>
                        <li>    .
                        </li>
                        <li><b>!</b></li>
                    </ul>
                </Trans>
                <h2 className="main-headers">{t("plansTitle")}</h2>
                <Trans i18nKey="plansList">
                    <ul className="tight-list">
                        <li>     ,     /
                               //<i> </i>.
                        </li>
                        <li> .    ?</li>
                    </ul>
                </Trans>
            </main>
            <footer className="footer-block">
                <div>
                    <GithubIcon />
                </div>
                <div>2018-2022 <span role="img" aria-label="cookieEmoji"></span></div>
            </footer>
        </div>
    );
}

export default App;
